import { apiClient,serverAddr } from "./httpClient";

function clientsAssessor() {
  return apiClient.get(`${serverAddr}/assessor/clients`).then(res => {
    return res.data.results;
  });
}



function newAssessorClient(name,state,county,config){

  const data =
      {
        name:name,
        country:"us",
        state:state,
        county:county,
        assessor_config:config
      };
  return apiClient.post(`${serverAddr}/assessor/clients/create`, data).then(res => {
    return res;
  });
}

function newAssessorTask(name,interval,client,enab,oneOff){
    const data = {
        name:name,
        interval:interval,
        client:client,
        enabled:enab,
        one_off:oneOff
    };
    return apiClient.post(`${serverAddr}/assessor/tasks/create`, data).then(res => {

        return res;
      });

}

function newAssessorDataOrigin(name,field,client,layer,extract) {
    const data = {
        name:name,
        field:field,
        layer:layer,
        client:client,
        extract:extract
    };
    // console.log(data);

    return apiClient.post(`${serverAddr}/assessor/data_origins/create`, data).then(res => {

        return res;
      });

}

function getIntervals(){
    return apiClient.get(`${serverAddr}/assessor/intervals`).then(res => {
    return res;
  });
}


function getTasks(){
    return apiClient.get(`${serverAddr}/assessor/tasks`).then(res => {
    return res;
  });
}

function getClient(id) {
  return apiClient.get(`${serverAddr}/assessor/clients/${id}`).then(res => {

      return res.data;
  });
}

function updateClient(id, client) {
  return apiClient.put(`/connectors/client/${id}`, client).then(res => {
    return res;
  });
}

function deleteClient(client) {
  const { id } = client;
  return apiClient.delete(`/connectors/client/${id}`);
}




function getDataOrigins(){
  return apiClient.get(`${serverAddr}/assessor/data_origins`).then(res => {
    return res.data;
  });
}
// DEPTH_PRIORITY,
//       AUTOTHROTTLE_TARGET_CONCURRENCY,
//       AUTOTHROTTLE_START_DELAY

function runSpider(
  data_origin,
  data_final_schema,
  config_name,
  column_origin,
  log_name,
  concurrent_request,
  download_delay,
  enabledProxy,
  AUTOTHROTTLE_ENABLED,
  RANDOM_UA_PER_PROXY,
  AUTOTHROTTLE_DEBUG,
  ROBOTSTXT_OBEY,
  PROXYMESH_TIMEOUT,
  DEPTH_PRIORITY,
  AUTOTHROTTLE_TARGET_CONCURRENCY,
  AUTOTHROTTLE_START_DELAY,
  middlewareList
) {
  return apiClient.get("/connectors/run/spider", {
    params: {
      data_origin,
      data_final_schema,
      config_name,
      column_origin,
      log_name,
      concurrent_request,
      download_delay,
      enabledProxy,
      AUTOTHROTTLE_ENABLED,
      RANDOM_UA_PER_PROXY,
      AUTOTHROTTLE_DEBUG,
      ROBOTSTXT_OBEY,
      PROXYMESH_TIMEOUT,
      DEPTH_PRIORITY,
      AUTOTHROTTLE_TARGET_CONCURRENCY,
      AUTOTHROTTLE_START_DELAY,
      middlewareList
    }
  });
}

function getCounties(stateName){
  return apiClient.get(`${serverAddr}/assessor/available/us/${stateName}`).then(res => {

    return Object.keys(res.data);
  });
}

function getConfigsAssesor(stateName,configName){
  return apiClient.get(`${serverAddr}/assessor/available/us/${stateName}/${configName}`).then(res => {

    return res.data;
  });
}

function getStates(){
  return apiClient.get(`${serverAddr}/assessor/available/us`).then(res => {
      console.log(Object.keys(res.data));
    return Object.keys(res.data);
  });
}





function getDataOriginFields(dataOriginName){
    return apiClient.post(`${serverAddr}/assessor/data_origins/columns`,
        {data_origin_table:dataOriginName}).then(res => {
            return res.data;
          });
}

function getConfigs(name) {
  return apiClient.get("/connectors/configs", {
    params: { name }
  });
}

export {
  clientsAssessor,
  deleteClient,
  updateClient,
  getClient,
  runSpider,
  getConfigs,
  getDataOrigins,
  getDataOriginFields,
  getCounties,
    getStates,
    getConfigsAssesor,
    newAssessorClient,
    getIntervals,
    newAssessorTask,
    getTasks,
    newAssessorDataOrigin
};
