import React, { Component } from "react";
import {
  Grid,
  Row,
  Col,
  FormGroup,
  ControlLabel,
  FormControl
} from "react-bootstrap";

import { Link } from "react-router-dom";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

import { Card } from "../../components/Card/Card.jsx";
import Button from "../../components/CustomButton/CustomButton.jsx";
import { newClient } from "../../api/clients";
import { validate, requireDisabledButton } from "../../validations/commons";
import NotificationSystem from "react-notification-system";
import { style } from "../../variables/Variables.jsx";
import Select from "react-select";
import {getConfigsAssesor, getCounties, getStates, newAssessorClient} from "../../api/clientsAssessor";

class ClientAssessorNew extends Component {
  state = {
        countyList:[],
        statesList:[],
        configList:[],
        currentCounty:"",
        currentState:"",
        currentConfig:"",
        isLoadingCounty:true,
        isLoadingState:true,
        isLoadingConfig:true,
    name: { value: "", isValid: null },
    loading: false
  };

  componentDidMount() {
    getStates().then(result =>
        {
            const optionsState = result.map(dat => {
            return { value: dat, label:dat };
            });
            this.setState({statesList : optionsState
               },
                ()=>{
                this.setState(
                    {isLoadingState : false}
                );
                });

        });
  }

  handleChange = e => {
    const state = {
      ...this.state,
      [e.target.name]: {
        ...this.state[e.target.name],
        value: e.target.value,
        isValid: validate(e.target.value)
      }
    };

    this.setState(state);
  };

  addNotification = _ => {
    const { name } = this.state;
    const notification = this.notificationSystem.current;
    notification.addNotification({
      message: (
        <div>
          Client <b>{name.value}</b> successfully saved.
        </div>
      ),
      level: "info"
    });
  };

  handleCounties = selectedCounty => {
        this.setState({currentCounty:selectedCounty, isLoadingConfig:true},
            ()=>
            {
              const {currentState} = this.state
              getConfigsAssesor(currentState.label,selectedCounty.label).then(
                  response =>{
                    const optionsField = response.map(field => {
                        return {value: field, label: field};
                    });
                    this.setState({configList : optionsField
                    },
                    ()=>{
                    this.setState(
                        {isLoadingConfig : false}
                    );
                    });

               })
            }
            );
    };

  handleConfigs = selectedConfig => {
        this.setState({currentConfig:selectedConfig});
    };

  handleState = selectedState => {
        this.setState({currentState:selectedState, isLoadingCounty:true},
            ()=>
            {getCounties(selectedState.label).then(
                response =>{
                const optionsField = response.map(field => {
                    return {value: field, label: field};
                });
                this.setState({countyList : optionsField
                },
                ()=>{
                this.setState(
                    {isLoadingCounty : false}
                );
                });

            }
            )
            });

    };

  onSubmit = e => {
    e.preventDefault();
    this.setState({ loading: true });

    const {name,currentState,currentCounty,currentConfig} = this.state
    newAssessorClient(name.value,currentState.label,currentCounty.label,currentConfig.label).then(res => {
      this.setState({
        name: { value: "", isValid: null },
        configList:[],
        currentCounty:"",
        currentConfig:"",
        isLoadingCounty:true,
        isLoadingConfig:true,
        loading: false
      });

      this.addNotification();
    });
  };

  notificationSystem = React.createRef();

  render() {
    const { loading, name, countyList,
      currentCounty,isLoadingCounty,statesList,isLoadingState,currentState,
        configList,currentConfig, isLoadingConfig
    } = this.state;

    return (
      <>
        <h3 className="title-page">Client new</h3>
        <div className="content">
          <ol role="navigation" aria-label="breadcrumbs" className="breadcrumb">
            <li>
              <Link to="/admin/dashboard">Dashboard</Link>
            </li>
            <li>
              <Link to="/admin/clientAssessor">Client List</Link>
            </li>
            <li className="active">
              <span>Client new</span>
            </li>
          </ol>

          <NotificationSystem ref={this.notificationSystem} style={style} />
          <Grid fluid>
            <Row>
              {loading && (
                <Loader
                  type="Circles"
                  color="rgb(161, 232, 44)"
                  height={100}
                  width={100}
                  className="loader"
                />
              )}

              <Col md={12}>
                {!loading && (
                  <Card
                    content={
                      <form onSubmit={this.onSubmit}>
                        <FormGroup
                          controlId="formValidationSuccess1"
                          validationState={name.isValid}
                        >
                          <ControlLabel>Client name</ControlLabel>
                          <FormControl
                            type="text"
                            name="name"
                            value={name.value}
                            onChange={this.handleChange}
                          />
                        </FormGroup>

                        <FormGroup>
                              <ControlLabel>Available States</ControlLabel>
                              <Select
                                placeholder={"Select State"}
                                options={statesList}
                                onChange={this.handleState}
                                value={currentState}
                                isLoading={isLoadingState}
                              />
                            </FormGroup>


                             <FormGroup>
                              <ControlLabel>Available Counties</ControlLabel>
                              <Select
                                placeholder={"Select County"}
                                options={countyList}
                                onChange={this.handleCounties}
                                value={currentCounty}
                                isLoading={isLoadingCounty}
                              />
                            </FormGroup>

                            <FormGroup>
                              <ControlLabel>Available Configs</ControlLabel>
                              <Select
                                placeholder={"Select Available Config"}
                                options={configList}
                                onChange={this.handleConfigs}
                                value={currentConfig}
                                isLoading={isLoadingConfig}
                              />
                            </FormGroup>

                        <Button
                          bsStyle="success"
                          pullRight
                          fill
                          type="submit"
                          disabled={requireDisabledButton(this.state, ["name"])}
                        >
                          Create client
                        </Button>
                        <div className="clearfix" />
                      </form>
                    }
                  />
                )}
              </Col>
            </Row>
          </Grid>
        </div>
      </>
    );
  }
}

export default ClientAssessorNew;
